 *       2、设置通用原子类
 */
/* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */
#app {
    height: 100%;
}
html {
    /*标准字体大小设置 14 像素「rem 参照对象」*/
    font-size: 14px;
    /*滚动事件发生在 html 元素上；JS 中可以监听 html 的滚动*/
    overflow-y: auto;
    /*让 html 和浏览器窗口高度一致*/
    height: 100%;
    /*少数浏览器默认背景色为浅灰色，所以设置默认背景颜色为纯白*/
    background-color: #fff;
}
html,
body {
    /*body 宽度大 html 度时，某些浏览器会出现内部滚动条；所以设置「html、body」宽度相同且「overflow-x: hidden」*/
    overflow-x: hidden;
    width: 100%;
    /*取消部分浏览器点击有阴影*/
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    /*优化移动端滚动事件*/
    -webkit-overflow-scrolling: touch;
    overflow-scrolling: touch;
}
/* 内外边距通常让各个浏览器样式的表现位置不同 */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    margin: 0;
    padding: 0;
}
body {
    /*设置基本字体配置*/
    font: 1rem 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft Yahei', Arial, sans-serif;
    /*让绝对定位元素，根据 body 定位*/
    position: relative;
    /*设置网页基本字体颜色为浅灰色*/
    color: #666;
    /*使字体渲染更顺滑*/
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}
input,
select,
textarea {
    font-size: 100%;
}
textarea {
    /*textarea 默认不可以放缩*/
    resize: none;
}
/* 去掉 table cell 的边距并让其边重合 */
table {
    border-collapse: collapse;
    border-spacing: 0;
}
/* ie bug：th 不继承 text-align */
th {
    text-align: inherit;
}
/* 去除默认边框 */
fieldset,
img {
    border: none;
}
/* ie6 7 8(q) bug 显示为行内表现 */
iframe {
    display: block;
}
/* 去掉 firefox 下此元素的边框 */
abbr,
acronym {
    border: none;
    font-variant: normal;
}
/* 一致的 del 样式 */
del {
    text-decoration: line-through;
}
address,
