code,
dfn,
em,
th,
var {
    font-style: normal;
    font-weight: 500;
}
/* 去掉列表前的标识，li 会继承 */
ol,
ul {
    list-style: none;
}
/* 对齐是排版最重要的因素，别让什么都居中 */
caption,
